.App {
  padding: 0 10vw;
  --shadow-size: 3px;
  --shadow-size-neg: calc(var(--shadow-size) * -1);
}

header {
  margin: 2em 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.invertable {
  text-shadow: var(--shadow-size-neg) var(--shadow-size-neg) rgb(255, 157, 0), var(--shadow-size) var(--shadow-size) rgb(125, 177, 255);
}

.invertable:hover {
  text-shadow: var(--shadow-size-neg) var(--shadow-size-neg) rgb(125, 177, 255), var(--shadow-size) var(--shadow-size) rgb(255, 157, 0);
  transform: scale(1.3);
}

h1 {
  margin: 0px;
  cursor: default;
  font-size: 64px;
}

h1 .muted {
  text-shadow: 2px 2px rgb(202, 202, 202);
}

nav {
  display: flex;
  gap: 1em;
  align-items: center;
}

nav > * {
  cursor: pointer;
  font-size: 24px;
}

nav > a.contact {
  padding: 8px;
  background-color: black;
  color: white;
  font-weight: bold;
}

nav > a {
  padding: 8px;
}

nav > a.active {
  outline: 2px solid black !important;
}

nav > a:hover {
  outline: 2px dotted grey;
}

h2 {
  margin-top: 0;
}

/* Home Page Main Content */

.intro-box {
  padding: 64px 0px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 4em
}

.intro-box p {
  font-size: 24px;
}

.hero-text {
  font-weight: bold;
  color: black;
  font-size: 48px;
}

.muted {
  color: #555;
}

.intro-images {
  border: 1px solid black;
  display: grid;
  place-items: center;
  background-color: #eee;
}

.showcase-box {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
}

.showcase-box > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Project Page */

.project-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2em;
  margin-bottom: 4em;
}

.project-item {
  border: 1px solid rgb(232, 232, 232);
  padding: 1em;
  display: flex;
  flex-direction: column;
  gap: 1em;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 1em;
}

h3 {
  margin: 0;
  font-size: 36px;
}

.project-image {
  width: 100%;
  border: 1px solid rgb(165, 165, 165);
}

.project-links {
  display: grid;
  grid-template-columns: 1fr 1fr;
  place-items: center;
}

.project-links a {
  font-size: 30px;
  padding: 8px 24px;
  background-color: black;
  outline: 2px solid black;
  color: white;
  text-shadow: 2px 2px black;
  transition: all 1s ease;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.project-links a:hover:first-child {
  background: #f09819;
  background: -webkit-linear-gradient(32deg, #f09819 0%, #edde5d 100%);
  background: linear-gradient(32deg, #f09819 0%, #edde5d 100%);
}

.project-links a:hover {
  background: #4776e6;
  background: -webkit-linear-gradient(223deg, #4776e6 0%, #8e54e9 100%);
  background: linear-gradient(223deg, #4776e6 0%, #8e54e9 100%);
}

.project-item p {
  font-size: 24px;
  margin: 0px;
}
